#overlay {
  background-color: rgba(77, 66, 66, 0.7);
  position: fixed;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: 100;
  display: flex;
  align-items: center;
  justify-content: center;
}
.gameoverbox {
  padding-top: 30px;
  background-color: rgb(41, 3, 3);
  color: white;
  width: 40%;
  height: 25%;
  border-radius: 3%;
  position: relative;
  align-items: center;
  justify-content: center;
}
#closeButton {
  position: absolute;
  background-color: transparent;
  border-color: aliceblue;
  color: red;
  top: 0;
  right: 0;
  outline: none;
}
