.grid-container {
  display: flex;
  background-color: black;
  justify-content: center;
  align-items: center;
}

.wrapper {
  display: grid;
  width: 80%;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: repeat(4, 1fr);
  grid-gap: 10px;
  /* background-color: black; */
  justify-content: center;
  align-items: center;
  margin-top: 10px;
  margin-bottom: 10px;
}
